import Vue from 'vue'
import Router from 'vue-router'

//路由:
//登录
const login = resolve => require(['@/views/login.vue'], resolve) // 登录页

//运营信息
const index = resolve => require(['@/views/index.vue'], resolve) // index
const bannerList = resolve => require(['@/views/operate-info/banner-list.vue'], resolve) // 首页轮播
const systemPush = resolve => require(['@/views/operate-info/system-push.vue'], resolve) // 系统推送
const operatingArticles = resolve => require(['@/views/operate-info/operating-articles.vue'], resolve) //  运营文章
const addArticle = resolve => require(['@/views/operate-info/add-article.vue'], resolve) // 添加/编辑 运营文章
const vehicleManage = resolve => require(['@/views/operate-info/vehicle-manage.vue'], resolve) //  车型库管理

//用户
const userInfo = resolve => require(['@/views/user/user-info.vue'], resolve) //  用户信息
const vehicleInfo = resolve => require(['@/views/user/vehicle-info.vue'], resolve) //  车辆信息
const ticketInfo = resolve => require(['@/views/user/ticket-info.vue'], resolve) // 发单信息
const userCredit = resolve => require(['@/views/user/user-credit.vue'], resolve) // 用户信用
const carOwnerInfo = resolve => require(['@/views/user/car-owner-info.vue'], resolve) // 车主信息

//订单
const order = resolve => require(['@/views/order/order.vue'], resolve) // 订单

//财务
// const userAccount = resolve => require(['@/views/finance/user-account.vue'], resolve) //  用户账户
// const userAccountInfo = resolve => require(['@/views/finance/user-account-info.vue'], resolve) // 用户账户信息
// const userAccountIncomeExpense = resolve => require(['@/views/finance/user-account-income-expense.vue'], resolve) // 用户账户收支明细
const platformAccount = resolve => require(['@/views/finance/platform-account.vue'], resolve) // 平台账户
const withdraw = resolve => require(['@/views/finance/withdraw.vue'], resolve) // 提现管理

//系统设置
const accountManage = r => require.ensure([], () => r(require('@/views/settings/account-manage.vue')), 'system-setting') // 车辆管理
const myAccount = r => require.ensure([], () => r(require('@/views/settings/my-account.vue')), 'system-setting') //  我的账户

//消息
const news = resolve => require(['@/views/news/news.vue'], resolve) // 消息

Vue.use(Router)

export default new Router({
  routes: [
    // 登录页
    {
      path: '/',
      name: 'login',
      alias: '/login',
      component: login
    }, {
      path: '/index',
      component: index,
      children: [
        {
          // 首页轮播
          path: '/',
          name: 'bennerList',
          component: bannerList,
        }, {
          // 系统推送
          path: '/system-push',
          name: 'systemPush',
          component: systemPush,
        }, {
          // 运营文章
          path: '/operating-articles',
          name: 'operatingArticles',
          component: operatingArticles
        }, {
          // 添加/编辑 文章
          path: '/add-article',
          name: 'addArticle',
          component: addArticle
        }, {
          // 车辆库
          path: '/vehicle-manage',
          name: 'vehicleManage',
          component: vehicleManage
        }, {
          // 用户信息
          path: '/user-info',
          name: 'userInfo',
          component: userInfo
        }, {
          // 车辆信息
          path: '/vehicle-info',
          name: 'vehicleInfo',
          component: vehicleInfo
        }, {
          // 发单信息
          path: '/ticket-info',
          name: 'ticketInfo',
          component: ticketInfo
        }, {
          // 用户信用
          path: '/user-credit',
          name: 'userCredit',
          component: userCredit
        }, {
          // 车东信息
          path: '/car-owner-info',
          name: 'carOwnerInfo',
          component: carOwnerInfo
        }, {
          // 订单
          path: '/order',
          name: 'order',
          component: order
        // }, {
        // // 用户账户
          // path: '/user-account',
          // name: 'userAccount',
          // component: userAccount
        // }, {
        // // 用户账户信息
          // path: '/user-account-info',
          // name: 'userAccountInfo',
          // component: userAccountInfo
        // }, {
        // // 用户账户收支明细
          // path: '/user-account-income-expense',
          // name: 'userAccountIncomeExpense',
          // component: userAccountIncomeExpense
        }, {
          // 平台账户
          path: '/platform-account',
          name: 'platformAccount',
          component: platformAccount
        }, {
          // 提现
          path: '/withdraw',
          name: 'withdraw',
          component: withdraw
        }, {
          // 账户管理
          path: '/account-manage',
          name: 'accountManage',
          component: accountManage
        }, {
          // 我的账户
          path: '/my-account',
          name: 'myAccount',
          component: myAccount
        }, {
          // 消息
          path: '/news',
          name: 'news',
          component: news
        }
      ]
    }
  ]
})


