// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

import router from './router' // 路由
import VueResource from 'vue-resource' // ajax

import {common} from './utils/common.js' // 公用方法
import store from './store' // 状态

import ElementUi from 'element-ui'
// import 'element-ui/lib/theme-default/index.css' //默认主题
import './assets/element-orange/index.css' //自定义主题

import vueQuillEditor from 'vue-quill-editor' //富文本编辑器

// 路由钩子
router.beforeEach((to, form, next) => {
	// 路由权限控制
	if (!common.checkPower(to.path)) {
		next(false)
		return
	}
   //放行
   next()
   store.commit('setActiveIndex', to.path.slice(1))
})

Vue.use(ElementUi)
Vue.use(vueQuillEditor)
Vue.use(VueResource)
Vue.prototype.$common = common // 挂载到原型

Vue.config.productionTip = false
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



